import * as React from 'react'
import PropTypes from 'prop-types'
import { styled } from '@mui/material/styles'
import Stepper from '@mui/material/Stepper'
import Step from '@mui/material/Step'
import StepLabel from '@mui/material/StepLabel'
import StepConnector, { stepConnectorClasses } from '@mui/material/StepConnector'
import { Container, useMediaQuery, useTheme } from '@mui/material'
import StepContent from '@mui/material/StepContent'
import Typography from '@mui/material/Typography'
import stepsConfig from './steps.config'

import SearchIcon from '@mui/icons-material/Search';
import FavoriteIcon from '@mui/icons-material/Favorite';
import HealthAndSafetyIcon from '@mui/icons-material/HealthAndSafety';

export default function StepProcess() {
  const [activeStep, setActiveStep] = React.useState(0)
  const theme = useTheme()
  const isMobile = useMediaQuery(theme.breakpoints.down('md'))

  return (
    <Container sx={{
      paddingTop: isMobile ? 8 : 16,
      paddingBottom: isMobile ? 4 : 8
    }}>
      {isMobile ? (
        <Typography variant='h3' gutterBottom mb={4}>
          Этапы
        </Typography>
      ) : (
        <Typography variant='h5' color={'text.secondary'} gutterBottom textAlign={isMobile ? 'left' : 'center'} mb={4}>
          Лечение в 3 этапа
      </Typography>
      )}
      {isMobile ? (
        <Stepper activeStep={activeStep} orientation='vertical'>
          {stepsConfig.map((step, index) => (
            <Step key={step.label} onClick={() => setActiveStep(index)}>
              <StepLabel StepIconComponent={ColorlibStepIcon}>
                <Typography variant={'h6'}>{step.label}</Typography>
              </StepLabel>
              <StepContent>
                <Typography>{step.description}</Typography>
              </StepContent>
            </Step>
          ))}
        </Stepper>
      ) : (
        <Stepper alternativeLabel activeStep={activeStep} connector={<ColorlibConnector />}>
          {stepsConfig.map((step, index) => (
            <Step key={step.label} onClick={() => setActiveStep(index)}>
              <StepLabel StepIconComponent={ColorlibStepIcon}>
                <Typography variant={'h6'}>{step.label}</Typography>
              </StepLabel>
              <StepContent sx={{ mt: 2 }}>
                <Typography>{step.description}</Typography>
              </StepContent>
            </Step>
          ))}
        </Stepper>
      )}
    </Container>
  )
}

function ColorlibStepIcon(props) {
  const { active, completed, className } = props

  const icons = {
    1: <SearchIcon />,
    2: <FavoriteIcon />,
    3: <HealthAndSafetyIcon />
  }

  return (
    <ColorlibStepIconRoot ownerState={{ completed, active }} className={className}>
    {icons[String(props.icon)]}
  </ColorlibStepIconRoot>
  )
}

ColorlibStepIcon.propTypes = {
  /**
   * Whether this step is active.
   * @default false
   */
  active: PropTypes.bool, className: PropTypes.string, /**
   * Mark the step as completed. Is passed to child components.
   * @default false
   */
  completed: PropTypes.bool, /**
   * The label displayed in the step icon.
   */
  icon: PropTypes.node
}

const ColorlibConnector = styled(StepConnector)(({ theme }) => ({
  [`&.${stepConnectorClasses.alternativeLabel}`]: {
    top: 22
  },
  [`&.${stepConnectorClasses.active}`]: {
    [`& .${stepConnectorClasses.line}`]: {
      backgroundImage: 'linear-gradient( 95deg,rgb(242,113,33) 0%,rgb(233,64,87) 50%,rgb(138,35,135) 100%)'
    }
  },
  [`&.${stepConnectorClasses.completed}`]: {
    [`& .${stepConnectorClasses.line}`]: {
      backgroundImage: 'linear-gradient( 95deg,rgb(242,113,33) 0%,rgb(233,64,87) 50%,rgb(138,35,135) 100%)'
    }
  },
  [`& .${stepConnectorClasses.line}`]: {
    height: 3,
    border: 0,
    backgroundColor: theme.palette.mode === 'dark' ? theme.palette.grey[800] : '#eaeaf0',
    borderRadius: 1
  }
}))

const ColorlibStepIconRoot = styled('div')(({ theme, ownerState }) => ({
  backgroundColor: theme.palette.mode === 'dark' ? theme.palette.grey[700] : '#ccc',
  zIndex: 1,
  color: '#fff',
  width: 50,
  height: 50,
  display: 'flex',
  borderRadius: '50%',
  justifyContent: 'center',
  alignItems: 'center', ...(ownerState.active && {
    backgroundImage: 'linear-gradient( 136deg, rgb(242,113,33) 0%, rgb(233,64,87) 50%, rgb(138,35,135) 100%)',
    boxShadow: '0 4px 10px 0 rgba(0,0,0,.25)'
  }), ...(ownerState.completed && {
    backgroundImage: 'linear-gradient( 136deg, rgb(242,113,33) 0%, rgb(233,64,87) 50%, rgb(138,35,135) 100%)'
  })
}))